}

 .navbar-items a {
   background-color: rgba(106,0,148);
   color: white;
   text-decoration: none;
   border-width: 0px;
   font-size: 1.3em;
   font-family: geo_sans_light;
 }

.navbar-items a:hover {
    color: orange;
    text-decoration: none;
    outline-style: none;
    cursor: pointer;
}

.logo a {
    color: white;
    text-decoration: none;
    font-size: 1.7rem;
    margin-left: 0.5rem;
}


@media screen and (max-width: 600px) {
    .navbar-items {
        display: none;
    }
}
@media screen and (min-width: 600px) {
    .hamburger-menu{
        width: 0;
    }
}

.hamburgerButton {
    margin: 10px;
    position: relative;
    width: 30px;
    height: 30px;
  }
  
  .line {
    position: absolute;
    height: 4px;
    width: 100%;
    background-color: lightGray;
    border-radius: 2px;
    transition: all cubic-bezier(0.25, 0.1, 0.3, 1.5) 0.3s;
  }
  
  #hamburger:hover .line {
    background-color: white;
  }
  
  .line1 {
    top: 5%;
  }
  .line2 {
    top: 43%;
  }
  .line3 {
    top: 81%;
  }
  
  #hamburger {
    visibility: hidden;
    cursor: pointer;
  }
  
  #hamburger.close .line1 {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: 48%;
  }
  
  #hamburger.close .line2, #hamburger.close .line3 {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    top: 48%;
  }
  
  
  
  #hamburgerMenu {
    visibility: visible;
  }
  
  #hamburgerMenu ul {
    display: inline-block;
    width: 100%;
    height: 100%;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #6c7373;
  }
  
  #hamburgerMenu ul li a:hover {
    background-color: white;
    text-decoration: underline;
  }

  @media screen and (max-width: 600px) {
 
    #hamburger {
      visibility: visible;
    }
    #hamburgerMenu li {
      width: 100%;
  
    }
  
    #hamburgerMenu {
      visibility: hidden;
      height: 0px;
    }
  
    #hamburgerMenu.close {
      height: auto;
      visibility: visible;
    }
  
  }
  
  
.side-drawer {
    height: 100%;
    background: white;
    box-shadow: 2px 0px 3px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 64px;
    left: 0;
    width: 70%;
    max-width: 300px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;

}

.side-drawer ul{
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer li {
    margin: 0.5rem 0;
    color: rgba(106,0,148);
    font-size: 1.3rem;
}

.side-drawer a{
    color: rgba(106,0,148);
    text-decoration: none;
}

.side-drawer a:hover, .side-drawer a:active {
    color: orange;
    text-decoration: none;
}



.open {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

.side-drawer-spacing {
    flex: 1.0 1;
}

@media screen and (min-width: 600px) {
    .side-drawer {
        display: none;
    }
   
}
